import { Action } from "./types";
import { data } from "../types";
import { apiState } from "../defaults";

export const setToken = (state = "", action: Action) => {
  switch (action.type) {
    case "SET":
      return action.token;
  }
  return state;
};
export const setLoading = (state = false, action: Action): boolean => {
  switch (action.type) {
    case "SET_LOADING":
      return action.loading ? action.loading : false;
  }
  return false;
};

export const checkToken = (state = { token: "" }, action: Action) => {
  switch (action.type) {
    case "CHECK":
      return { ...state, ...action.payload };
  }
  return { ...state };
};

export const login = (state = apiState, action: Action) => {
  switch (action.type) {
    case "LOGIN":
      return { ...state, ...action.payload };
  }
  return { ...state };
};

export const register = (state = apiState, action: Action) => {
  switch (action.type) {
    case "REGISTER":
      return { ...state, ...action.payload };
  }
  return { ...state };
};
export const fetchLocations = (state = apiState, action: Action) => {
  switch (action.type) {
    case "FETCH_LOCATIONS":
      return action.payload;
  }
  return { ...state };
};

export const setModuleU = (state = "", action: Action): string => {
  switch (action.type) {
    case "SET_MODULE":
      return action.module ? action.module : state;
  }
  return state;
};

export const setAuth = (state = false, action: Action): boolean => {
  switch (action.type) {
    case "SET_AUTH":
      return action ? true : false;
  }
  return state;
};

export const setLocationData = (state = {}, action: Action): data => {
  switch (action.type) {
    case "SET_LOCATION_DATA":
      return { ...action.data };
  }
  return state;
};
